
.main {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: space-evenly;
    border: 1px solid black;
    border-radius: 10px;
    background-color: #f8d7a4;
}

.currency {
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    border: 1px solid green;
    border-radius: 10px;
    background-color: #ffae6b;
    font-family: 'Cormorant', serif;
}

.selector {
    height: 30px;
    border-radius: 5px;
    text-align: center;
}

.result {
    text-align: center;
    width: 70%;
    height: 55px;
    margin: 0;
}

.result > p {
    margin: 0;
}

.input {
    height: 26px;
    border-radius: 5px;
    border: 1px solid;
}

.date {
    background-color: #ffae6b;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 60px;
    border-radius: 5px;
    border: 1px solid green;
}

.date input {
    border: 1px solid;
    border-radius: 5px;
    height: 25px;
}

.submitButton {
    /*margin: 10px;*/
    border-radius: 10px;
    box-shadow: 5px 5px 13px 1px black;
    font-weight: bolder;
    background-color: #cc7444;
    font-family: 'Caveat', cursive;
}

.submitButton:active {
    box-shadow: 0 0 0 0;
}

.dateAndButton {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;

}